import React from 'react'
import Typography from '@material-ui/core/Typography'
import '../App.css';
import Iframe from 'react-iframe'

class ProbGraph extends React.Component {
  constructor(props) {
    super(props);

  }

  render() {
  var prob = Math.round(this.props.prob)
  var src=  "http://odds-view.appspot.com/api/draw?odds="+prob +":100&shape=person_rect&color1=BBBBBB&color2=dd5542"
  return (<img src={src} className={'grid_img norm_padding'}/>)
  }
}

export default ProbGraph;
